import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/login',
        name: 'login',
        component: () => import(/* webpackChunkName: "login" */'@/views/login/LoginView.vue')
    },
    {
        path: '/',
        name: "layout",
        component: () => import(/* webpackChunkName: "layout" */'@/views/laylout/layOut.vue'),
        children: [
            {
                path: '/home',
                name: 'home',
                component: () => import(/* webpackChunkName: "home" */'@/views/home/HomeView.vue')
            }
        ]
    },
    {
        path: '*',
        name: 'errorPage',
        component: () => import(/* webpackChunkName: "errorPage" */'@/views/errorPage/errorPage.vue')
    }
]


const router = new VueRouter({routes})
if(localStorage.getItem('luYou')){
    let menus = JSON.parse(localStorage.getItem('luYou')).userMenus;
    menus.forEach(item => {
        router.addRoute("layout", {
            path: item.path,
            name: item.path.split("/")[1],
            component:()=>import(`@/views${item.src}`)
        })
    })
}



export default router
