import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token:window.localStorage.getItem('token') || null,
    username:window.localStorage.getItem('username') || null,
    projectId:window.localStorage.getItem('projectId') || null,
    suiteId:window.localStorage.getItem('suiteId')  || null,
    projectName:window.localStorage.getItem('projectName')  || null,
    envId:[],
    portId:window.localStorage.getItem('portId')  || null,
    luYou: window.localStorage.getItem('luYou')  || ['login'],
  },
  getters: {},
  mutations: {
    // 登陆接口返回的token放在state，通过window设置在本地存储中
    setUserToken(state,payload){
      state.token = payload
      window.localStorage.setItem('token',payload)
    },
    // 保存登陆返回的路由信息
    saveUserRoute(state,payload){
      state.luYou = payload
      window.localStorage.setItem('luYou',JSON.stringify({userMenus:payload}))
    },
    // 登陆的用户数据放在state，通过window设置在本地存储中
    saveUser(state, payload){

      state.username = payload
      window.localStorage.setItem('username',payload)
    },
    // 计划的id存放在vuex中
    saveProjectId(state, payload){
      state.projectId = payload
      window.localStorage.setItem('projectId', payload)
    },
    // 计划的名字存放在vuex中
    saveProjectName(state, payload){
      state.projectName = payload
      window.localStorage.setItem('projectName', payload)
    },
    // 套件id存放在vuex中
    saveSuiteId(state,payload){
      state.suiteId=payload
      window.localStorage.setItem('suiteId',payload)
    },
    // 环境id存放在vuex中
    saveEnvId(state,payload){
      state.envId=payload
      window.localStorage.setItem('envId',payload)
    },
    // 报告id存放在vuex中
    saveReportId(state,payload){
      state.portId=payload
      window.localStorage.setItem('portId',payload)
    }
  },
  actions: {},
  modules: {}
})
